import React, { ReactNode, useContext, CSSProperties, forwardRef } from "react";
import { ConfigContext } from '../config/config-provider'; // Import the ConfigContext

interface SheetProps {
  children: ReactNode;
  className?: string;
  style?: CSSProperties;
  pageNo: number;
  showBleed?: boolean;
  overlay?: (args: {pageNo: number}) => ReactNode;
  'data-page-key'?: string;
}

const Sheet = forwardRef<HTMLDivElement, SheetProps>(({
  children,
  className = "",
  style,
  pageNo,
  overlay,
  showBleed,
  'data-page-key': dataPageKey,
}, ref) => {
  const config = useContext(ConfigContext);
  const showBleedArea = showBleed ?? config?.page?.showBleed ?? false;

  return (
    <div 
      className={`uhuu-page-sheet ${className}`} 
      style={style} 
      ref={ref}
      data-page-key={dataPageKey}
    >
      {children}
      {overlay && overlay({pageNo})}
      {showBleedArea && <div className="uhuu-bleed-area"></div>}
    </div>
  );
});

export default Sheet;