import * as React from "react"
import { MoreHorizontal } from "lucide-react"
import type { ImageOption } from './image-with-options'
import { useShouldHideUI } from "../editor-shell/interactive-mode-context"
import { Button } from "../ui/button"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "../ui/dropdown-menu"
import { cn } from "../ui/cn"

type OptionSelectEvent = Event | React.SyntheticEvent

export const handleOptionSelect = (event: OptionSelectEvent, option: ImageOption) => {
  event.stopPropagation()

  if (option.onSelect) {
    option.onSelect(event)
    return
  }

  if (option.dialog && typeof window !== "undefined") {
    ;(window as any).$uhuu?.editDialog?.(option.dialog)
  }
}

export const renderOverlay = (
  overlaySvg: string | undefined,
  overlayClassName: string | undefined
): React.ReactNode => {
  if (!overlaySvg) return null
  
  const trimmed = overlaySvg.trim()
  const isInlineSvg = trimmed.startsWith("<")

  if (isInlineSvg) {
    const normalizedSvg = trimmed.replace(/<svg\b([^>]*)>/i, (match, attrs) => {
      let nextAttrs = attrs
      if (/\bwidth=/.test(nextAttrs)) {
        nextAttrs = nextAttrs.replace(/\bwidth=(\"[^\"]*\"|'[^']*'|[^\s>]+)/i, 'width="100%"')
      } else {
        nextAttrs += ' width="100%"'
      }
      if (/\bheight=/.test(nextAttrs)) {
        nextAttrs = nextAttrs.replace(/\bheight=(\"[^\"]*\"|'[^']*'|[^\s>]+)/i, 'height="100%"')
      } else {
        nextAttrs += ' height="100%"'
      }
      if (/\bpreserveAspectRatio=/.test(nextAttrs)) {
        nextAttrs = nextAttrs.replace(
          /\bpreserveAspectRatio=(\"[^\"]*\"|'[^']*'|[^\s>]+)/i,
          'preserveAspectRatio="xMidYMid slice"'
        )
      } else {
        nextAttrs += ' preserveAspectRatio="xMidYMid slice"'
      }
      return `<svg${nextAttrs}>`
    })
    return (
      <div
        className={cn("pointer-events-none absolute inset-0 z-10", overlayClassName)}
        aria-hidden="true"
        dangerouslySetInnerHTML={{ __html: normalizedSvg }}
      />
    )
  }

  return (
    <img
      src={overlaySvg}
      alt=""
      aria-hidden="true"
      className={cn(
        "pointer-events-none absolute inset-0 z-10 h-full w-full object-cover",
        overlayClassName
      )}
    />
  )
}

export const renderOptionsButton = (
  options: ImageOption[],
  showOptions: boolean
): React.ReactNode => {
  if (!showOptions) return null
  
  return (
    <div className="absolute right-2 top-2 z-20">
      <DropdownMenu modal={false}>
        <DropdownMenuTrigger asChild>
          <Button
            variant="secondary"
            size="icon"
            title="Image options"
            className="h-7 w-7 shadow-sm"
            onPointerDown={(event) => event.stopPropagation()}
            onClick={(event) => event.stopPropagation()}
          >
            <MoreHorizontal className="h-4 w-4" />
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent className="w-40 p-1.5" align="end">
          {options.map((option) => (
            <DropdownMenuItem
              key={option.id}
              onSelect={(event) => handleOptionSelect(event, option)}
              disabled={option.disabled}
            >
              {option.icon && <span className="mr-2 inline-flex">{option.icon}</span>}
              <span>{option.label}</span>
            </DropdownMenuItem>
          ))}
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  )
}

export const useShouldShowOptions = (
  options: ImageOption[] = []
): boolean => {
  const shouldHideUI = useShouldHideUI()
  return options.length > 0 && !shouldHideUI
}
