import * as React from "react"
import { useContext } from "react"
import { ConfigContext } from "../config/config-provider"
import { getDialogProps } from "../editable/dialog-props"
import ImageBleed from './image-bleed'
import type { ImageOption } from './image-with-options'
import {
  renderOverlay,
  renderOptionsButton,
  useShouldShowOptions,
} from './image-overlay-utils'

interface ImageBleedWithOverlayProps {
  className?: string
  style?: React.CSSProperties
  overlaySvg?: string
  overlayClassName?: string
  options?: ImageOption[]
  dialog?: Record<string, any> | null
  dialogProps?: Record<string, any>
  bleedProps: any
  children?: React.ReactNode
}

const ImageBleedWithOverlay = ({
  className,
  style,
  overlaySvg,
  overlayClassName,
  options = [],
  dialog,
  dialogProps,
  bleedProps,
  children,
}: ImageBleedWithOverlayProps) => {
  const config = useContext(ConfigContext)
  const showOptions = useShouldShowOptions(options)
  
  // Get dialog props for click-to-edit
  const baseDialogProps = dialog ? getDialogProps({ dialog }, config) : {}
  
  // Merge base dialog props with additional dialogProps
  const mergedDialogProps = React.useMemo(() => {
    if (!dialogProps) return baseDialogProps
    const merged: Record<string, any> = { ...baseDialogProps, ...dialogProps }
    if (baseDialogProps.className || dialogProps.className) {
      merged.className = `${baseDialogProps.className || ''} ${dialogProps.className || ''}`.trim()
    }
    Object.keys(baseDialogProps).forEach((key) => {
      const baseHandler = baseDialogProps[key]
      const extraHandler = dialogProps[key]
      if (key.startsWith("on") && typeof baseHandler === "function" && typeof extraHandler === "function") {
        merged[key] = (event: any) => {
          baseHandler(event)
          extraHandler(event)
        }
      }
    })
    return merged
  }, [baseDialogProps, dialogProps])

  // Render bleed with overlay inside
  // Pass dialog prop to ImageBleed (it will call getDialogProps internally)
  // No relative wrapper - it breaks absolute positioning!
  return (
    <>
      <ImageBleed {...bleedProps} dialog={dialog}>
        {renderOverlay(overlaySvg, overlayClassName)}
        {children}
      </ImageBleed>
      {renderOptionsButton(options, showOptions)}
    </>
  )
}

export default ImageBleedWithOverlay
