import * as React from "react"

interface PortalRootContextType {
  portalContainer: HTMLElement | null
}

const PortalRootContext = React.createContext<PortalRootContextType>({
  portalContainer: null,
})

export function usePortalRoot() {
  return React.useContext(PortalRootContext)
}

interface PortalRootProviderProps {
  children: React.ReactNode
}

export function PortalRootProvider({ children }: PortalRootProviderProps) {
  const [portalContainer, setPortalContainer] = React.useState<HTMLElement | null>(null)

  React.useEffect(() => {
    if (typeof document === 'undefined') return

    // Create portal container
    const container = document.createElement('div')
    container.setAttribute('data-uhuu-portal', '')
    container.style.cssText = 'position: fixed; top: 0; left: 0; z-index: 9999;'

    document.body.appendChild(container)
    setPortalContainer(container)

    return () => {
      document.body.removeChild(container)
    }
  }, [])

  return (
    <PortalRootContext.Provider value={{ portalContainer }}>
      {children}
    </PortalRootContext.Provider>
  )
}
