import * as React from "react"
import { useMemo } from "react"
import ImageBleed from './image-bleed'
import ImageBleedWithOverlay from './image-bleed-with-overlay'
import ImageSpread from './image-spread'
import ImageWithOptions from './image-with-options'
import type { ImageOption } from './image-with-options'

type ImageBlockMode = "bleed" | "spread" | "auto"

type ImageRenderProps = React.ImgHTMLAttributes<HTMLImageElement>

// Annotation data structure
type AnnotationValue = {
  annotationSvg?: string
  annotations?: unknown[]
}

// Simplified annotation config
interface ImageBlockAnnotation {
  path: string                        // where annotation data is stored
  value?: AnnotationValue             // preloaded data (optional)
  [key: string]: any                  // any extra config (visualGallery, etc.)
}

// Dialog config with type
interface ImageBlockDialog {
  path: string                        // where data is stored
  type: 'image' | 'satellite'         // dialog type
  [key: string]: any                  // any extra config (ratio, lat, lng, zoom, etc.)
}

interface ImageBlockProps {
  // Image source
  src?: string
  alt?: string // Optional - not required for PDF rendering
  
  // Styling - className/style for container, imageClassName/imageStyle for <img>
  className?: string
  imageClassName?: string
  style?: React.CSSProperties
  imageStyle?: React.CSSProperties
  backgroundColor?: string
  
  // Print mode - only needed for full-bleed print images
  mode?: ImageBlockMode
  side?: "start" | "end" // For spread mode (left/right page)
  
  // Bleed dimensions (only used in bleed/spread modes)
  width?: number
  height?: number
  left?: number
  right?: number
  top?: number
  bottom?: number
  pageWidth?: number
  pageHeight?: number
  bleed?: number
  
  // Simplified dialog config (auto-generates edit dialog with type)
  dialog?: ImageBlockDialog
  
  // Simplified annotation config (auto-generates overlay + annotate option)
  annotation?: ImageBlockAnnotation
  
  // Manual interactive features (for advanced use cases)
  overlaySvg?: string
  overlayClassName?: string
  options?: ImageOption[]
  dialogProps?: Record<string, any>
  placeholder?: React.ReactNode
  children?: React.ReactNode
  imageProps?: React.ImgHTMLAttributes<HTMLImageElement>
  renderImage?: (props: ImageRenderProps) => React.ReactNode
}

const ImageBlock = (props: ImageBlockProps) => {
  // Auto-generate overlay and options from annotation/dialog config
  const { computedOverlaySvg, computedOptions, computedDirectDialog } = useMemo(() => {
    const { annotation, dialog, overlaySvg, options, src } = props
    
    // If no simplified props, use manual props as-is
    if (!annotation && !dialog) {
      return {
        computedOverlaySvg: overlaySvg,
        computedOptions: options,
        computedDirectDialog: undefined,
      }
    }
    
    // Extract annotation data
    const annotationValue = annotation?.value || {}
    
    // Auto-generate overlay from annotation
    const autoOverlaySvg = overlaySvg ?? annotationValue.annotationSvg ?? ''
    
    // Build options array
    const autoOptions: ImageOption[] = []
    
    // Only add options if annotation is enabled
    // (dialog alone doesn't show options dropdown)
    if (annotation) {
      // Add "Edit image" option if dialog config is provided
      if (dialog) {
        const dialogConfig: any = {
          ...dialog, // Spread everything (path, type, ratio, etc.)
        }
        
        // For satellite dialogs, wrap lat/lng/zoom in config.config
        if (dialog.type === 'satellite') {
          const { path, type, ...satelliteParams } = dialog
          dialogConfig.config = {
            ...satelliteParams,
            path: 'image',
          }
          dialogConfig.path = path
          dialogConfig.type = type
        } else if (dialog.type === 'image') {
          // For image dialogs, add required fields that buildPageDialog adds
          if (!dialogConfig.imagePath) {
            dialogConfig.imagePath = 'url'
          }
        }
        
        autoOptions.push({
          id: 'edit',
          label: 'Edit image',
          dialog: dialogConfig,
        })
      }
      
      // Add "Annotate" option
      const annotations = Array.isArray(annotationValue.annotations) 
        ? annotationValue.annotations 
        : []
      
      const { path, value, annotations: _, ...annotationConfig } = annotation
      
      const annotateDialogConfig: any = {
        path: annotation.path,
        type: 'annotation',
        image: src,
        annotations,
        ...annotationConfig, // Spread extra config (visualGallery, etc.)
      }
      
      autoOptions.push({
        id: 'annotate',
        label: 'Annotate',
        dialog: annotateDialogConfig,
      })
    }
    
    // Merge auto-generated options with manual options
    const finalOptions = options ? [...autoOptions, ...options] : autoOptions
    
    // If dialog exists, always create directDialog for click-to-edit
    // This allows clicking the image to open edit dialog, even when dropdown exists
    let directDialog = undefined
    if (dialog) {
      const dialogConfig: any = {
        ...dialog, // Spread everything (path, type, ratio, etc.)
      }
      
      // For satellite dialogs, wrap lat/lng/zoom in config.config
      if (dialog.type === 'satellite') {
        const { path, type, ...satelliteParams } = dialog
        dialogConfig.config = {
          ...satelliteParams,
          path: 'image',
        }
        dialogConfig.path = path
        dialogConfig.type = type
      } else if (dialog.type === 'image') {
        // For image dialogs, add required fields that buildPageDialog adds
        if (!dialogConfig.imagePath) {
          dialogConfig.imagePath = 'url'
        }
      }
      
      directDialog = dialogConfig
    }
    
    return {
      computedOverlaySvg: autoOverlaySvg,
      computedOptions: finalOptions.length > 0 ? finalOptions : undefined,
      computedDirectDialog: directDialog,
    }
  }, [props.annotation, props.dialog, props.overlaySvg, props.options, props.src])
  
  // Determine mode: auto (default), bleed (for print), or spread (for print with sides)
  const mode: ImageBlockMode = useMemo(() => {
    if (props.mode) return props.mode
    if (props.side !== undefined) return "spread"
    
    // Auto-detect: if bleed dimensions are provided, use bleed mode
    const hasBleedDimensions = props.width !== undefined || props.height !== undefined || 
                               props.left !== undefined || props.right !== undefined ||
                               props.top !== undefined || props.bottom !== undefined
    
    return hasBleedDimensions ? "bleed" : "auto"
  }, [props.mode, props.side, props.width, props.height, props.left, props.right, props.top, props.bottom])
  
  // Check if we need ImageWithOptions wrapper
  const needsImageWithOptions = 
    mode === "auto" || // Auto mode always uses ImageWithOptions
    (computedOptions && computedOptions.length > 0) || 
    computedOverlaySvg || 
    computedDirectDialog || // Need wrapper for click-to-edit
    props.renderImage !== undefined ||
    props.placeholder !== undefined ||
    props.children !== undefined
  
  // Extract props
  const {
    mode: _mode,
    side,
    src,
    alt,
    className,
    imageClassName,
    style,
    imageStyle,
    backgroundColor,
    width,
    height,
    left,
    right,
    top,
    bottom,
    pageWidth,
    pageHeight,
    bleed,
    overlayClassName,
    dialogProps,
    placeholder,
    children,
    imageProps,
    renderImage,
  } = props
  
  // Base component props (for bleed/spread modes)
  const baseProps = {
    src,
    backgroundColor,
    width,
    height,
    left,
    right,
    top,
    bottom,
    pageWidth,
    pageHeight,
    bleed,
  }
  
  // Auto mode: Simple image with wrapper if needed
  if (mode === "auto") {
    return (
      <ImageWithOptions
        src={src}
        alt={alt}
        className={className}
        style={style}
        imageClassName={imageClassName}
        imageStyle={imageStyle}
        overlaySvg={computedOverlaySvg}
        overlayClassName={overlayClassName}
        options={computedOptions}
        dialog={computedDirectDialog}
        dialogProps={dialogProps}
        placeholder={placeholder}
        children={children}
        imageProps={imageProps}
        renderImage={renderImage}
      />
    )
  }
  
  // Spread mode: for print with left/right pages
  if (mode === "spread") {
    const spreadComponent = (
      <ImageSpread
        {...baseProps}
        side={side}
      />
    )
    
    if (needsImageWithOptions) {
      return (
        <ImageWithOptions
          className={className}
          style={style}
          overlaySvg={computedOverlaySvg}
          overlayClassName={overlayClassName}
          options={computedOptions}
          dialog={computedDirectDialog}
          dialogProps={dialogProps}
        >
          {spreadComponent}
          {children}
        </ImageWithOptions>
      )
    }
    
    return spreadComponent
  }
  
  // Bleed mode: for print with bleed calculations
  // For bleed mode with overlay/options, or with direct dialog, use ImageBleedWithOverlay
  if (needsImageWithOptions && (computedOverlaySvg || computedOptions?.length || computedDirectDialog)) {
    return (
      <ImageBleedWithOverlay
        className={className}
        style={style}
        overlaySvg={computedOverlaySvg}
        overlayClassName={overlayClassName}
        options={computedOptions}
        dialog={computedDirectDialog}
        dialogProps={dialogProps}
        bleedProps={baseProps}
      >
        {children}
      </ImageBleedWithOverlay>
    )
  }
  
  // Simple bleed without overlay/options
  return (
    <ImageBleed {...baseProps} />
  )
}

export default ImageBlock
export type { ImageOption }
