/**
 * Check if the code is running in development mode.
 * Checks if running on localhost or local development domains.
 * 
 * @returns {boolean} True if running in development mode, false otherwise
 */
export function isDev(): boolean {
  // Only check runtime environment, not build-time variables
  if (typeof window !== 'undefined') {
    const hostname = window.location.hostname;
    return hostname === 'localhost' || 
           hostname === '127.0.0.1' ||
           hostname.endsWith('.local') ||
           window.location.port !== '';  // Any port means dev server
  }
  
  return false;
}
